.campaign-admin__campaign {
  position: relative;
  height: 100%;
}

@media (max-width: 991px) {
  .campaign-admin__campaign.campaign-admin__campaign--hide-preview .campaign-admin__campaign-preview {
    display: none;
  }

  .campaign-admin__campaign .list-group-item.active {
    background-color: #fff;
    color: #4f5861;
    z-index: 0;
  }
}

@media (max-width: 991px) and (max-width: 1199px) {
  .campaign-admin__campaign .list-group-item.active {
    border: none;
  }
}

@media (max-width: 991px) {
  .campaign-admin__campaign .list-group-item.active:hover {
    background-color: #f5f5f5;
    color: #4f5861;
    border-color: #e5e8eb;
  }
}

.campaign-admin__campaign-items {
  width: 100%;
  z-index: 1;
  position: relative;
}

@media (min-width: 992px) {
  .campaign-admin__campaign-items {
    width: 300px;
  }
}

.campaign-admin__item-links,
.campaign-admin__item-links--has-links,
.campaign-admin__item-links--is-linked {
  display: flex;
  align-items: center;
  color: #29abe2;
  position: absolute;
  right: 1.2308rem;
  bottom: 0.82053rem;
  font-size: 0.923rem;
}

.campaign-admin__item-links .font-icon-link,
.campaign-admin__item-links--has-links .font-icon-link,
.campaign-admin__item-links--is-linked .font-icon-link {
  font-size: 16px;
  position: relative;
  top: 3px;
  margin-right: 1px;
  margin-left: 3px;
}

.campaign-admin__item-links .campaign-admin__item-links__number,
.campaign-admin__item-links--has-links .campaign-admin__item-links__number,
.campaign-admin__item-links--is-linked .campaign-admin__item-links__number {
  opacity: 0;
}

.list-group-item.active .campaign-admin__item-links--has-links {
  opacity: 1;
}

@media (min-width: 992px) {
  .list-group-item.active .campaign-admin__item-links--has-links {
    color: #fff;
  }
}

.list-group-item.active .campaign-admin__item-links__number {
  opacity: 1;
}

.campaign-admin__item-links--is-linked {
  opacity: 1;
}

.campaign-admin__item-links--is-linked .campaign-admin__item-links__number {
  display: none;
}

.list-group-item:hover .campaign-admin__item-links--is-linked {
  opacity: 1;
}




